import { format } from "date-fns";
import { notFound } from "next/navigation";
import Header from "@/components/Header";
import LeftSidebar from "@/components/LeftSidebar";
import RightSidebar from "@/components/RightSidebar";
import Footer from "@/components/Footer";
import { SanityContentRenderer } from "@/lib/sanityContent";
import { urlFor } from "@/lib/sanity";
// import { getNewsUpdate } from "@/hooks/useSanityServer";
import { ArrowLeft, Eye } from "lucide-react";
import Link from "next/link";

export async function generateMetadata({ params }: any) {
  const newsUpdate = await getNewsUpdate(params.slug);

  if (!newsUpdate) return {};

  const metaTitle = newsUpdate.seo?.metaTitle || newsUpdate.title;
  const metaDescription =
    newsUpdate.seo?.metaDescription ||
    newsUpdate.excerpt ||
    `Read ${newsUpdate.title} - Latest legal news and updates from Commercial Law Chamber`;

  const canonicalUrl = `https://www.clclaw.in/news/${newsUpdate.slug.current}`;

  const ogImage = newsUpdate.featuredImage
    ? urlFor(newsUpdate.featuredImage).width(1200).height(630).fit("crop").url()
    : "https://www.clclaw.in/clc-logo.png";

  return {
    title: `${metaTitle} | Commercial Law Chamber`,
    description: metaDescription,
    alternates: { canonical: canonicalUrl },
    openGraph: {
      title: metaTitle,
      description: metaDescription,
      url: canonicalUrl,
      type: "article",
      images: [ogImage],
    },
    twitter: {
      card: "summary_large_image",
      title: metaTitle,
      description: metaDescription,
      images: [ogImage],
    },
  };
}

const getTypeDisplayName = (type: string) => {
  switch (type) {
    case "case-update": return "Case Update";
    case "news": return "News";
    case "publication": return "Publication";
    case "deal-corner": return "Deal Corner";
    default: return type;
  }
};

export default async function NewsPage({ params }: any) {
  const newsUpdate = await getNewsUpdate(params.slug);

  if (!newsUpdate) return notFound();

  const publishedDate = newsUpdate.publishedAt
    ? new Date(newsUpdate.publishedAt).toISOString()
    : new Date().toISOString();

  const newsType = getTypeDisplayName(newsUpdate.type);

  const featuredImage = newsUpdate.featuredImage
    ? urlFor(newsUpdate.featuredImage).width(800).height(400).fit("crop").url()
    : "";

  return (
    <div className="min-h-screen">
      <div className="max-w-7xl bg-slate-900/40 mx-auto px-4 sm:px-6 lg:px-8 py-4">
        <Header />

        <div className="flex flex-col lg:flex-row gap-4">
          {/* Left Sidebar */}
          <div className="hidden lg:block lg:w-48 xl:w-52">
            <LeftSidebar activeSection="content" />
          </div>

          {/* Main Article */}
          <div className="flex-1 min-w-0">
            <article className="bg-white/95 backdrop-blur-sm p-8">
              <div className="max-w-4xl">

                {/* Back Button */}
                <Link
                  href="/news"
                  className="flex items-center gap-2 text-gray-600 hover:text-gray-800 mb-6"
                >
                  <ArrowLeft className="h-4 w-4" />
                  Back to News
                </Link>

                <h1 className="text-3xl lg:text-4xl font-bold poppins text-[#163C0F] mb-6">
                  {newsUpdate.title}
                </h1>

                <span className="inline-block bg-primary/10 text-primary px-3 py-1 rounded-full text-sm font-medium mb-3">
                  {newsType}
                </span>

                <time className="block text-sm text-gray-600 mb-6">
                  {format(new Date(publishedDate), "MMMM d, yyyy")}
                </time>

                {/* Featured Image */}
                {featuredImage && (
                  <img
                    src={featuredImage}
                    alt={newsUpdate.featuredImage?.alt || newsUpdate.title}
                    className="w-full rounded-lg shadow-md mb-8"
                  />
                )}

                {/* Main Content */}
                <div className="text-gray-700">
                  <SanityContentRenderer content={newsUpdate.content} />
                </div>

                {/* Download Button */}
                {newsUpdate.downloadUrl && (
                  <div className="flex items-center gap-3 mt-8">
                    <a
                      href={newsUpdate.downloadUrl}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="border px-4 py-2 rounded flex items-center gap-2"
                    >
                      <Eye className="h-4 w-4" />
                      Read The Full Document Here
                    </a>
                  </div>
                )}
              </div>
            </article>
          </div>

          {/* Right Sidebar */}
          <div className="hidden lg:block lg:w-60 xl:w-64">
            <RightSidebar />
          </div>
        </div>

        <Footer />
      </div>
    </div>
  );
}
